import "../../style/personal.css";

import React, { Component } from "react";
import { Link } from "react-router";
import Navbars from "../Navbars/Navbars.jsx";
import OuterHeader from "../Headers/OuterHeader.jsx";

class PersonalPage extends Component {
  render() {
    return (
      <div className="personal-page">
        <OuterHeader title={""} />

        <div className="userInfo fine-border-bottom">
          <div className="line">
            <div className="avatar">
              <img src="/image/icon/avatar.png" alt="avatar" />
            </div>
            <div className="nickname">
              <div className="name"></div>
              <p className="desc">hello react</p>
            </div>
          </div>
        </div>

        <div className="personal-list fine-border-bottom">
          <div className="line fine-border-bottom">
            <div className="icon icon0">
              {/* <img src="/image/icon/shoucang.png" alt="" /> */}
            </div>
            <div className="txt"></div>
          </div>

          <div className="line fine-border-bottom">
            <div className="icon icon1">
              {/* <img src="/image/icon/dongtai.png" alt="" /> */}
            </div>
            <div className="txt"></div>
          </div>

          <div className="line">
            <div className="icon icon2">
              {/* <img src="/image/icon/wenzhang.png" alt="" /> */}
            </div>
            <div className="txt"></div>
          </div>
        </div>

        <div className="personal-list fine-border-bottom">
          <div className="line fine-border-bottom">
            <div className="icon icon3">
              {/* <img src="/image/icon/fadeback.png" alt="" /> */}
            </div>
            <div className="txt"></div>
          </div>
          <div className="line">
            <div className="icon icon4">
              {/* <img src="/image/icon/setting.png" alt="" /> */}
            </div>
            <div className="txt"></div>
          </div>
        </div>

        <div>
          <Link to="/login">
            <span></span>
          </Link>
        </div>
        <div>
          <Link to="/register">
            <span></span>
          </Link>
        </div>

        {/* <div>
          <Link to="/myEditor">
            <span></span>
          </Link>
        </div> */}

        <Navbars tabIndex={3} />
      </div>
    );
  }
}

export default PersonalPage;



